@import '../_colors.scss';
@import '../_typography.scss';

.text {
  @include default-text;
  box-sizing: border-box;
  color: $text-color;

  & > p {
    margin-top: 0;
    margin-bottom: 0.75em;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &.highlightLinks {
    a {
      color: map-get($all-colors, link);
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }

      &:active {
        color: map-get($all-colors, blue800);
      }
    }
  }
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.center {
  text-align: center;
}

.justify {
  text-align: justify;
}

.bold {
  @include bold;
}

.medium {
  @include medium;
}

.light {
  @include light;
}

.italic {
  @include italic;
}

.truncate {
  @include truncate;
}

.underline {
  @include underline;
}

@each $name, $color in $all-colors {
  .#{$name} {
    color: $color;
  }
}
